import React, { Component } from "react";
import RunSelector from "./RunSelector";
import PercentSelector from "./PercentSelector";
import ProfitLossSelector from "./ProfitLossSelector";
import AeIvSelector from "./AeIvSelector";

class ReportSelection extends Component {
  constructor(props) {
    super(props);

    this.updateClick = this.updateClick.bind(this);

    this.state = {
      runs: "",
      wins: "",
      placed: "",
      winPercent: "",
      placedPercent: "",
      winProfitLoss: "",
      placedProfitLoss: "",
      AE: "",
      IV: ""
    };
  }

  onChange = (source, value) => {
    switch (source) {
      case "Runs":
        this.setState({ runs: parseInt(value) });
        break;
      case "Wins":
        this.setState({ wins: parseInt(value) });
        break;
      case "Placed":
        this.setState({ placed: parseInt(value) });
        break;
      case "Win%":
        this.setState({ winPercent: parseFloat(value) });
        break;
      case "Place%":
        this.setState({ placedPercent: parseFloat(value) });
        break;
      case "WinPL":
        this.setState({ winProfitLoss: parseFloat(value) });
        break;
      case "PlacedPL":
        this.setState({ placedProfitLoss: parseFloat(value) });
        break;
      case "AE":
        this.setState({ AE: parseFloat(value) });
        break;
      case "IV":
        this.setState({ IV: parseFloat(value) });
        break;
      default:
        break;
    }
  };

  updateClick = () => {
    this.props.onUpdate(this.state);
  };

  resetClick = () => {
    this.setState({
      runs: "",
      wins: "",
      placed: "",
      winPercent: "",
      placedPercent: "",
      winProfitLoss: "",
      placedProfitLoss: "",
      AE: "",
      IV: ""
    });
    this.props.onReset(); /* invoke callback supplied in props */
  };

  render() {
    return (
      <div className="container mb-3 mt-3">
        <RunSelector
          title="Runs"
          onChange={this.onChange}
          value={this.state.runs}
        />
        <RunSelector
          title="Wins"
          onChange={this.onChange}
          value={this.state.wins}
        />
        <RunSelector
          title="Placed"
          onChange={this.onChange}
          value={this.state.placed}
        />
        <PercentSelector
          title="Win%"
          onChange={this.onChange}
          value={this.state.winPercent}
        />
        <PercentSelector
          title="Place%"
          onChange={this.onChange}
          value={this.state.placedPercent}
        />
        <ProfitLossSelector
          title="WinPL"
          onChange={this.onChange}
          value={this.state.winProfitLoss}
        />
        <ProfitLossSelector
          title="PlacedPL"
          onChange={this.onChange}
          value={this.state.placedProfitLoss}
        />
        <AeIvSelector
          title="AE"
          onChange={this.onChange}
          value={this.state.AE}
        />
        <AeIvSelector
          title="IV"
          onChange={this.onChange}
          value={this.state.IV}
        />
        <button
          className="btn btn-outline-success mb-3 mr-3"
          style={{ height: "70px" }}
          onClick={this.updateClick}
        >
          Update
        </button>
        <button
          className="btn btn-outline-warning mb-3"
          style={{
            height: "70px",
            minWidth: "80px"
          }}
          onClick={this.resetClick}
        >
          Reset
        </button>
      </div>
    );
  }
}

export default ReportSelection;
